"use client";

import { createContext, useContext, forwardRef, InputHTMLAttributes, ReactNode } from "react";
import { cn } from "./lib/utils";

// Context for RadioGroup
const RadioGroupContext = createContext<{
  name?: string;
  value?: string;
  onChange?: (value: string) => void;
} | null>(null);

interface RadioGroupProps {
  children: ReactNode;
  name: string;
  value?: string;
  defaultValue?: string;
  onChange?: (value: string) => void;
  label?: string;
  error?: string;
  className?: string;
  orientation?: "horizontal" | "vertical";
}

export function RadioGroup({
  children,
  name,
  value,
  onChange,
  label,
  error,
  className,
  orientation = "vertical",
}: RadioGroupProps) {
  const orientationClass = orientation === "horizontal" ? "flex-row gap-6" : "flex-col gap-3";

  return (
    <RadioGroupContext.Provider value={{ name, value, onChange }}>
      <fieldset className={className}>
        {label && (
          <legend className={cn("mb-3 text-sm font-medium", error ? "text-error" : "text-text")}>
            {label}
          </legend>
        )}
        <div className={cn("flex", orientationClass)}>{children}</div>
        {error && <p className="mt-2 text-xs text-error">{error}</p>}
      </fieldset>
    </RadioGroupContext.Provider>
  );
}

interface RadioProps extends Omit<InputHTMLAttributes<HTMLInputElement>, "type"> {
  label?: string;
  description?: string;
  value: string;
}

export const Radio = forwardRef<HTMLInputElement, RadioProps>(
  ({ className, label, description, value, id, ...props }, ref) => {
    const context = useContext(RadioGroupContext);
    const inputId = id || `${context?.name}-${value}`;
    const isChecked = context?.value === value;

    const handleChange = () => {
      context?.onChange?.(value);
    };

    return (
      <div className={cn("flex items-start gap-3", className)}>
        <div className="relative flex h-5 items-center">
          <input
            ref={ref}
            type="radio"
            id={inputId}
            name={context?.name}
            value={value}
            checked={isChecked}
            onChange={handleChange}
            className="peer h-5 w-5 cursor-pointer appearance-none rounded-full border-2 border-border bg-surface-1 transition-colors checked:border-accent focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-accent/50 disabled:cursor-not-allowed disabled:opacity-50"
            {...props}
          />
          {/* Inner dot */}
          <div className="pointer-events-none absolute left-1 top-1 h-3 w-3 scale-0 rounded-full bg-accent transition-transform peer-checked:scale-100" />
        </div>
        {(label || description) && (
          <label htmlFor={inputId} className="cursor-pointer select-none">
            {label && <span className="text-sm font-medium text-text">{label}</span>}
            {description && (
              <span className="block text-xs text-text-3">{description}</span>
            )}
          </label>
        )}
      </div>
    );
  }
);

Radio.displayName = "Radio";
