import { ButtonHTMLAttributes, forwardRef } from "react";
import { cn } from "./lib/utils";

interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  variant?: "primary" | "secondary" | "ghost";
  size?: "sm" | "md" | "lg";
}

export const Button = forwardRef<HTMLButtonElement, ButtonProps>(
  (
    { className, variant = "primary", size = "md", children, ...props },
    ref
  ) => {
    const baseStyles =
      "relative inline-flex items-center justify-center font-semibold transition-all duration-150 focus-visible:outline-none disabled:pointer-events-none disabled:opacity-50";

    const variants = {
      primary: `
        bg-accent
        text-white
        shadow-sm
        hover:bg-accent-hover
        active:bg-accent-pressed active:shadow-inner
      `,
      secondary: `
        bg-surface-2
        text-text
        border border-border
        shadow-[0_1px_0_0_rgba(255,255,255,0.03)_inset]
        hover:bg-surface-3 hover:border-text-3/30
        active:bg-surface-1
      `,
      ghost: `
        text-text-2
        hover:text-text hover:bg-surface-2
        active:bg-surface-3
      `,
    };

    const sizes = {
      sm: "h-8 px-3 text-xs rounded-lg gap-1.5",
      md: "h-10 px-4 text-sm rounded-lg gap-2",
      lg: "h-12 px-6 text-base rounded-xl gap-2",
    };

    return (
      <button
        ref={ref}
        className={cn(baseStyles, variants[variant], sizes[size], className)}
        {...props}
      >
        {children}
      </button>
    );
  }
);

Button.displayName = "Button";
