"use client";

import { useState, useRef } from "react";
import { useToast } from "./Toast";
import { Badge } from "./Badge";
import { DropdownMenu } from "./DropdownMenu";
import { IconButton } from "./IconButton";
import { cn } from "./lib/utils";

// ===========================
// Roset FileTree Component
// ===========================
// A comprehensive filesystem visualization showcasing:
// - Real folder hierarchy with expand/collapse
// - Instant move/rename (metadata transactions)
// - ACL permissions with inheritance
// - Share links and team access
// - Versioning indicators
// - Storage backend info (S3/R2)
// - Context actions: move, rename, share, delete, undo

export type Permission = "owner" | "editor" | "viewer" | "inherited";
export type StorageProvider = "s3" | "r2" | "minio" | "local";

export interface FileNode {
  id: string;
  name: string;
  type: "file" | "folder";
  children?: FileNode[];
  size?: string;
  modified?: string;
  expanded?: boolean;
  // Roset-specific metadata
  permission?: Permission;
  sharedWith?: string[];
  isPublicLink?: boolean;
  versions?: number;
  storageKey?: string;
  storageProvider?: StorageProvider;
  lastOperation?: {
    type: "upload" | "move" | "rename" | "share" | "delete" | "restore";
    timestamp: string;
  };
}

interface FileTreeProps {
  initialData: FileNode[];
  className?: string;
  showMetadata?: boolean;
  showActions?: boolean;
  onNodeSelect?: (node: FileNode) => void;
  onNodeMove?: (nodeId: string, targetId: string) => void;
}

// Permission badge colors
const permissionConfig: Record<Permission, { label: string; variant: "success" | "info" | "warning" | "default" }> = {
  owner: { label: "Owner", variant: "success" },
  editor: { label: "Editor", variant: "info" },
  viewer: { label: "Viewer", variant: "default" },
  inherited: { label: "Inherited", variant: "warning" },
};

// Storage provider icons
const storageProviderConfig: Record<StorageProvider, { label: string; color: string }> = {
  s3: { label: "S3", color: "text-orange-400" },
  r2: { label: "R2", color: "text-amber-400" },
  minio: { label: "MinIO", color: "text-pink-400" },
  local: { label: "Local", color: "text-text-3" },
};

// File type icons
const getFileIcon = (name: string) => {
  const ext = name.split(".").pop()?.toLowerCase();
  switch (ext) {
    case "png":
    case "jpg":
    case "jpeg":
    case "gif":
    case "svg":
    case "webp":
      return (
        <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
        </svg>
      );
    case "pdf":
      return (
        <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 21h10a2 2 0 002-2V9.414a1 1 0 00-.293-.707l-5.414-5.414A1 1 0 0012.586 3H7a2 2 0 00-2 2v14a2 2 0 002 2z" />
        </svg>
      );
    case "md":
    case "mdx":
    case "txt":
      return (
        <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
        </svg>
      );
    case "json":
    case "yaml":
    case "yml":
    case "ts":
    case "tsx":
    case "js":
    case "jsx":
      return (
        <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 20l4-16m4 4l4 4-4 4M6 16l-4-4 4-4" />
        </svg>
      );
    case "zip":
    case "tar":
    case "gz":
      return (
        <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 8h14M5 8a2 2 0 110-4h14a2 2 0 110 4M5 8v10a2 2 0 002 2h10a2 2 0 002-2V8m-9 4h4" />
        </svg>
      );
    default:
      return (
        <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
        </svg>
      );
  }
};

export function FileTree({ 
  initialData, 
  className, 
  showMetadata = true,
  showActions = true,
  onNodeSelect,
  onNodeMove,
}: FileTreeProps) {
  const [data, setData] = useState(initialData);
  const [selectedId, setSelectedId] = useState<string | null>(null);
  const [dragOverId, setDragOverId] = useState<string | null>(null);
  const [renamingId, setRenamingId] = useState<string | null>(null);
  const [renameValue, setRenameValue] = useState("");
  const [searchQuery, setSearchQuery] = useState("");
  const [isSearching, setIsSearching] = useState(false);
  const { addToast } = useToast();
  const inputRef = useRef<HTMLInputElement>(null);
  const searchInputRef = useRef<HTMLInputElement>(null);

  // Search results with full path info
  interface SearchResult {
    node: FileNode;
    path: string;
    matchType: "name" | "extension" | "path";
  }

  // Recursive search across all nodes
  function searchNodes(nodes: FileNode[], query: string, currentPath = ""): SearchResult[] {
    const results: SearchResult[] = [];
    const lowerQuery = query.toLowerCase();
    
    for (const node of nodes) {
      const nodePath = currentPath ? `${currentPath}/${node.name}` : node.name;
      const lowerName = node.name.toLowerCase();
      
      // Match by name
      if (lowerName.includes(lowerQuery)) {
        results.push({ node, path: nodePath, matchType: "name" });
      }
      // Match by extension
      else if (node.type === "file" && node.name.includes(".")) {
        const ext = node.name.split(".").pop()?.toLowerCase() || "";
        if (ext.includes(lowerQuery)) {
          results.push({ node, path: nodePath, matchType: "extension" });
        }
      }
      
      // Recurse into children
      if (node.children) {
        results.push(...searchNodes(node.children, query, nodePath));
      }
    }
    
    return results;
  }

  // Get search results
  const searchResults = searchQuery.length >= 2 ? searchNodes(data, searchQuery) : [];
  
  // Handle search input
  const handleSearch = (query: string) => {
    setSearchQuery(query);
    if (query.length >= 2) {
      setIsSearching(true);
    } else {
      setIsSearching(false);
    }
  };

  // Clear search
  const clearSearch = () => {
    setSearchQuery("");
    setIsSearching(false);
    searchInputRef.current?.focus();
  };

  // Select search result and expand path to it
  const selectSearchResult = (result: SearchResult) => {
    // Expand all folders in the path
    const pathParts = result.path.split("/");
    let currentPath = "";
    
    setData((prev) => {
      let newData = [...prev];
      for (let i = 0; i < pathParts.length - 1; i++) {
        currentPath = currentPath ? `${currentPath}/${pathParts[i]}` : pathParts[i];
        // Find and expand this folder
        const folderName = pathParts[i];
        newData = expandFolderByName(newData, folderName);
      }
      return newData;
    });
    
    setSelectedId(result.node.id);
    setIsSearching(false);
    setSearchQuery("");
    addToast({ 
      title: `Found in ${result.node.storageProvider?.toUpperCase() || "storage"}`, 
      description: `/${result.path}`,
      variant: "info" 
    });
  };

  // Helper to expand folder by name
  function expandFolderByName(nodes: FileNode[], folderName: string): FileNode[] {
    return nodes.map((node) => {
      if (node.name === folderName && node.type === "folder") {
        return { ...node, expanded: true };
      }
      if (node.children) {
        return { ...node, children: expandFolderByName(node.children, folderName) };
      }
      return node;
    });
  }

  // Highlight matching text
  function highlightMatch(text: string, query: string) {
    if (!query || query.length < 2) return text;
    const lowerText = text.toLowerCase();
    const lowerQuery = query.toLowerCase();
    const index = lowerText.indexOf(lowerQuery);
    if (index === -1) return text;
    
    return (
      <>
        {text.slice(0, index)}
        <span className="bg-accent/30 text-accent font-semibold">{text.slice(index, index + query.length)}</span>
        {text.slice(index + query.length)}
      </>
    );
  }
  // Helper: Find and update a node recursively
  function updateNode(nodes: FileNode[], id: string, updater: (node: FileNode) => FileNode): FileNode[] {
    return nodes.map((node) => {
      if (node.id === id) {
        return updater(node);
      }
      if (node.children) {
        return { ...node, children: updateNode(node.children, id, updater) };
      }
      return node;
    });
  }

  // Helper: Find a node by ID
  function findNode(nodes: FileNode[], id: string): FileNode | null {
    for (const node of nodes) {
      if (node.id === id) return node;
      if (node.children) {
        const found = findNode(node.children, id);
        if (found) return found;
      }
    }
    return null;
  }

  // Helper: Remove node from tree
  function removeNode(nodes: FileNode[], id: string): FileNode[] {
    return nodes
      .filter((node) => node.id !== id)
      .map((node) => {
        if (node.children) {
          return { ...node, children: removeNode(node.children, id) };
        }
        return node;
      });
  }

  // Toggle folder expand/collapse
  const handleToggle = (id: string) => {
    setData((prev) => updateNode(prev, id, (node) => ({ ...node, expanded: !node.expanded })));
  };

  // Select a node
  const handleSelect = (node: FileNode) => {
    setSelectedId(node.id);
    onNodeSelect?.(node);
  };

  // Start renaming
  const startRename = (node: FileNode) => {
    setRenamingId(node.id);
    setRenameValue(node.name);
    setTimeout(() => inputRef.current?.focus(), 0);
  };

  // Complete rename (instant metadata operation)
  const completeRename = (id: string) => {
    if (!renameValue.trim()) {
      setRenamingId(null);
      return;
    }
    setData((prev) =>
      updateNode(prev, id, (node) => ({
        ...node,
        name: renameValue,
        lastOperation: { type: "rename", timestamp: "just now" },
      }))
    );
    addToast({ 
      title: "Renamed instantly", 
      description: "Metadata transaction committed in 12ms", 
      variant: "success" 
    });
    setRenamingId(null);
  };

  // Handle drag start
  const handleDragStart = (e: React.DragEvent, node: FileNode) => {
    e.dataTransfer.setData("nodeId", node.id);
    e.dataTransfer.effectAllowed = "move";
  };

  // Handle drag over
  const handleDragOver = (e: React.DragEvent, node: FileNode) => {
    e.preventDefault();
    if (node.type === "folder") {
      setDragOverId(node.id);
    }
  };

  // Handle drag leave
  const handleDragLeave = () => {
    setDragOverId(null);
  };

  // Handle drop (instant move - metadata transaction)
  const handleDrop = (e: React.DragEvent, targetNode: FileNode) => {
    e.preventDefault();
    setDragOverId(null);

    const sourceId = e.dataTransfer.getData("nodeId");
    if (!sourceId || sourceId === targetNode.id || targetNode.type !== "folder") return;

    const sourceNode = findNode(data, sourceId);
    if (!sourceNode) return;

    // Remove from original location
    let newData = removeNode(data, sourceId);
    
    // Add to new location
    newData = updateNode(newData, targetNode.id, (node) => ({
      ...node,
      expanded: true,
      children: [...(node.children || []), {
        ...sourceNode,
        lastOperation: { type: "move", timestamp: "just now" },
      }],
    }));

    setData(newData);
    onNodeMove?.(sourceId, targetNode.id);
    addToast({ 
      title: "Moved instantly", 
      description: "No copy/delete — pure metadata transaction (8ms)", 
      variant: "success" 
    });
  };

  // Share file/folder
  const handleShare = (node: FileNode) => {
    setData((prev) =>
      updateNode(prev, node.id, (n) => ({
        ...n,
        isPublicLink: true,
        lastOperation: { type: "share", timestamp: "just now" },
      }))
    );
    addToast({ 
      title: "Share link created", 
      description: `Signed URL generated • Expires in 1 hour`, 
      variant: "success" 
    });
  };

  // Delete (soft delete to trash)
  const handleDelete = (node: FileNode) => {
    setData((prev) => removeNode(prev, node.id));
    addToast({ 
      title: "Moved to trash", 
      description: "Soft delete — can be restored for 30 days", 
      variant: "info",
    });
  };

  // Undo last operation
  const handleUndo = (node: FileNode) => {
    addToast({ 
      title: "Operation undone", 
      description: `Restored "${node.name}" from audit log`, 
      variant: "success" 
    });
  };

  // Context menu items
  const getContextMenuItems = (node: FileNode) => [
    { label: "Rename", onClick: () => startRename(node) },
    { label: "Share", onClick: () => handleShare(node) },
    "separator" as const,
    { label: "Move to...", onClick: () => addToast({ title: "Move dialog", variant: "info" }) },
    { label: "Copy path", onClick: () => {
      navigator.clipboard.writeText(`/${node.name}`);
      addToast({ title: "Path copied", variant: "info" });
    }},
    "separator" as const,
    ...(node.lastOperation ? [{ label: "Undo " + node.lastOperation.type, onClick: () => handleUndo(node) }] : []),
    { label: "Delete", danger: true, onClick: () => handleDelete(node) },
  ];

  // Render tree nodes
  const renderTree = (nodes: FileNode[], depth = 0) => {
    return nodes.map((node) => {
      const isSelected = selectedId === node.id;
      const isDragOver = dragOverId === node.id;
      const isRenaming = renamingId === node.id;
      const hasPermission = node.permission && node.permission !== "inherited";
      const hasSharing = node.sharedWith?.length || node.isPublicLink;
      const hasVersions = node.versions && node.versions > 1;

      return (
        <div key={node.id}>
          <div
            className={cn(
              "group flex items-center gap-2 px-2 py-1.5 cursor-pointer rounded-md transition-all text-sm",
              isSelected
                ? "bg-accent/10 text-accent"
                : isDragOver
                ? "bg-accent/20 border-accent border-dashed border"
                : "text-text-2 hover:bg-surface-2 hover:text-text"
            )}
            style={{ paddingLeft: `${depth * 1.25 + 0.5}rem` }}
            onClick={() => {
              if (!isRenaming) {
                if (node.type === "folder") handleToggle(node.id);
                handleSelect(node);
              }
            }}
            draggable={!isRenaming}
            onDragStart={(e) => handleDragStart(e, node)}
            onDragOver={(e) => handleDragOver(e, node)}
            onDragLeave={handleDragLeave}
            onDrop={(e) => handleDrop(e, node)}
          >
            {/* Expand/collapse chevron for folders */}
            {node.type === "folder" ? (
              <span className={cn("flex-shrink-0 transition-transform", node.expanded ? "rotate-90" : "")}>
                <svg className="h-3 w-3 text-text-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                </svg>
              </span>
            ) : (
              <span className="w-3" />
            )}

            {/* Icon */}
            <span className={cn("flex-shrink-0", isSelected ? "text-accent" : "text-text-3")}>
              {node.type === "folder" ? (
                <svg className="h-4 w-4" fill="currentColor" viewBox="0 0 24 24">
                  <path d="M19.5 21a1.5 1.5 0 001.5-1.5v-11a1.5 1.5 0 00-1.5-1.5H13l-2-3H4a1.5 1.5 0 00-1.5 1.5v14A1.5 1.5 0 003.5 21h16z" />
                </svg>
              ) : (
                getFileIcon(node.name)
              )}
            </span>

            {/* Name (or rename input) */}
            {isRenaming ? (
              <input
                ref={inputRef}
                type="text"
                value={renameValue}
                onChange={(e) => setRenameValue(e.target.value)}
                onBlur={() => completeRename(node.id)}
                onKeyDown={(e) => {
                  if (e.key === "Enter") completeRename(node.id);
                  if (e.key === "Escape") setRenamingId(null);
                }}
                className="flex-1 bg-surface-3 px-1 py-0.5 rounded text-xs font-mono text-text focus:outline-none focus:ring-1 focus:ring-accent"
                onClick={(e) => e.stopPropagation()}
              />
            ) : (
              <span className="flex-1 truncate font-mono text-xs">{node.name}</span>
            )}

            {/* Metadata badges (when showMetadata is true) */}
            {showMetadata && !isRenaming && (
              <div className="flex items-center gap-1.5 opacity-0 group-hover:opacity-100 transition-opacity">
                {/* Permission */}
                {hasPermission && (
                  <Badge variant={permissionConfig[node.permission!].variant} size="sm">
                    {permissionConfig[node.permission!].label}
                  </Badge>
                )}
                
                {/* Sharing indicator */}
                {hasSharing && (
                  <span className="text-accent" title={node.isPublicLink ? "Public link" : `Shared with ${node.sharedWith?.length}`}>
                    <svg className="h-3.5 w-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6.001l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.368 2.684 3 3 0 00-5.368-2.684z" />
                    </svg>
                  </span>
                )}

                {/* Versions */}
                {hasVersions && (
                  <span className="text-xs text-text-3 font-mono" title={`${node.versions} versions`}>
                    v{node.versions}
                  </span>
                )}
              </div>
            )}

            {/* Size */}
            {node.size && (
              <span className="text-[10px] text-text-3 opacity-60 font-mono group-hover:opacity-100 transition-opacity">
                {node.size}
              </span>
            )}

            {/* Storage provider badge */}
            {showMetadata && node.storageProvider && (
              <span 
                className={cn(
                  "text-[9px] font-mono opacity-0 group-hover:opacity-100 transition-opacity",
                  storageProviderConfig[node.storageProvider].color
                )}
                title={`Stored in ${storageProviderConfig[node.storageProvider].label}`}
              >
                {storageProviderConfig[node.storageProvider].label}
              </span>
            )}

            {/* Last operation indicator */}
            {node.lastOperation && (
              <span className="text-[10px] text-accent opacity-60" title={`Last: ${node.lastOperation.type}`}>
                •
              </span>
            )}

            {/* Context menu */}
            {showActions && (
              <div className="opacity-0 group-hover:opacity-100 transition-opacity" onClick={(e) => e.stopPropagation()}>
                <DropdownMenu
                  trigger={
                    <IconButton variant="ghost" size="sm" aria-label="Actions">
                      <svg className="h-3.5 w-3.5" fill="currentColor" viewBox="0 0 24 24">
                        <path d="M12 8c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2zm0 2c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2zm0 6c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2z" />
                      </svg>
                    </IconButton>
                  }
                  items={getContextMenuItems(node)}
                />
              </div>
            )}
          </div>

          {/* Children */}
          {node.type === "folder" && node.expanded && node.children && (
            <div className="border-l border-border/40 ml-[calc(0.5rem+11px)]">
              {renderTree(node.children, depth + 1)}
            </div>
          )}
        </div>
      );
    });
  };

  const totalFiles = countNodes(data, "file");
  const totalFolders = countNodes(data, "folder");

  return (
    <div className={cn("rounded-xl border border-border bg-surface-1 overflow-hidden", className)}>
      {/* Header */}
      <div className="flex items-center justify-between border-b border-border px-3 py-2 bg-surface-2/50">
        <div className="flex items-center gap-2">
          <span className="text-xs font-semibold uppercase tracking-wider text-text-3">Filesystem</span>
          <Badge variant="outline" size="sm">{totalFolders} folders</Badge>
          <Badge variant="outline" size="sm">{totalFiles} files</Badge>
        </div>
        <div className="flex items-center gap-2">
          {/* Traffic light dots */}
          <div className="flex gap-1">
            <div className="h-2 w-2 rounded-full bg-error/40" />
            <div className="h-2 w-2 rounded-full bg-warning/40" />
            <div className="h-2 w-2 rounded-full bg-success/40" />
          </div>
        </div>
      </div>

      {/* Search Bar - Recursive search across all buckets */}
      <div className="border-b border-border bg-surface-2/30 px-3 py-2">
        <div className="relative">
          <div className="absolute left-2.5 top-1/2 -translate-y-1/2 text-text-3">
            <svg className="h-3.5 w-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
          </div>
          <input
            ref={searchInputRef}
            type="text"
            value={searchQuery}
            onChange={(e) => handleSearch(e.target.value)}
            placeholder="Search across all buckets... (S3, R2, MinIO)"
            className="w-full h-8 pl-8 pr-8 rounded-md border border-border bg-surface-1 text-xs text-text placeholder:text-text-3/60 focus:outline-none focus:ring-1 focus:ring-accent focus:border-accent transition-colors"
          />
          {searchQuery && (
            <button
              onClick={clearSearch}
              className="absolute right-2.5 top-1/2 -translate-y-1/2 text-text-3 hover:text-text transition-colors"
            >
              <svg className="h-3.5 w-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          )}
        </div>

        {/* Search Results Dropdown */}
        {isSearching && searchResults.length > 0 && (
          <div className="mt-2 rounded-md border border-border bg-surface-1 shadow-lg max-h-[200px] overflow-y-auto">
            <div className="px-2 py-1.5 border-b border-border bg-surface-2/50">
              <span className="text-[10px] font-medium text-text-3 uppercase tracking-wider">
                {searchResults.length} result{searchResults.length !== 1 ? "s" : ""} across all buckets
              </span>
            </div>
            {searchResults.map((result) => (
              <button
                key={result.node.id}
                onClick={() => selectSearchResult(result)}
                className="w-full flex items-center gap-2 px-2 py-2 hover:bg-surface-2 transition-colors text-left border-b border-border/50 last:border-b-0"
              >
                {/* File/Folder Icon */}
                <span className="text-text-3 flex-shrink-0">
                  {result.node.type === "folder" ? (
                    <svg className="h-4 w-4" fill="currentColor" viewBox="0 0 24 24">
                      <path d="M19.5 21a1.5 1.5 0 001.5-1.5v-11a1.5 1.5 0 00-1.5-1.5H13l-2-3H4a1.5 1.5 0 00-1.5 1.5v14A1.5 1.5 0 003.5 21h16z" />
                    </svg>
                  ) : (
                    <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                    </svg>
                  )}
                </span>
                
                {/* Name with highlight */}
                <div className="flex-1 min-w-0">
                  <div className="text-xs font-medium text-text truncate">
                    {highlightMatch(result.node.name, searchQuery)}
                  </div>
                  <div className="text-[10px] text-text-3 font-mono truncate">
                    /{result.path}
                  </div>
                </div>

                {/* Storage Provider Badge */}
                {result.node.storageProvider && (
                  <span className={cn("text-[9px] font-mono px-1.5 py-0.5 rounded-full bg-surface-3",
                    result.node.storageProvider === "s3" ? "text-orange-400" :
                    result.node.storageProvider === "r2" ? "text-amber-400" :
                    result.node.storageProvider === "minio" ? "text-pink-400" : "text-text-3"
                  )}>
                    {result.node.storageProvider.toUpperCase()}
                  </span>
                )}

                {/* Size */}
                {result.node.size && (
                  <span className="text-[10px] text-text-3 font-mono">{result.node.size}</span>
                )}
              </button>
            ))}
          </div>
        )}

        {/* No results message */}
        {isSearching && searchQuery.length >= 2 && searchResults.length === 0 && (
          <div className="mt-2 rounded-md border border-border bg-surface-1 px-3 py-4 text-center">
            <span className="text-xs text-text-3">No files found for &ldquo;{searchQuery}&rdquo;</span>
          </div>
        )}
      </div>

      {/* Breadcrumb / path bar */}
      <div className="border-b border-border bg-surface-1/50 px-3 py-1.5 flex items-center gap-1 text-[11px] font-mono text-text-3">
        <span className="text-accent">/</span>
        <span>root</span>
        {selectedId && (
          <>
            <span className="text-text-3/40">/</span>
            <span className="text-text-2">{findNode(data, selectedId)?.name || ""}</span>
          </>
        )}
      </div>

      {/* Tree */}
      <div className="max-h-[400px] overflow-y-auto p-2">
        {renderTree(data)}
      </div>

      {/* Footer status */}
      <div className="border-t border-border bg-surface-2/30 px-3 py-1.5 flex items-center justify-between text-[10px] text-text-3">
        <span>Roset Control Plane • Recursive search across all storage backends</span>
        <span className="flex items-center gap-1">
          <span className="inline-block h-1.5 w-1.5 rounded-full bg-success animate-pulse" />
          Connected
        </span>
      </div>
    </div>
  );
}

// Helper to count nodes
function countNodes(nodes: FileNode[], type: "file" | "folder"): number {
  let count = 0;
  for (const node of nodes) {
    if (node.type === type) count++;
    if (node.children) count += countNodes(node.children, type);
  }
  return count;
}
