"use client";

import { ReactNode, useState, useRef, useEffect } from "react";
import { cn } from "./lib/utils";

interface DropdownItem {
  label: string;
  onClick?: () => void;
  icon?: ReactNode;
  danger?: boolean;
  disabled?: boolean;
}

interface DropdownMenuProps {
  trigger: ReactNode;
  items: (DropdownItem | "separator")[];
  align?: "start" | "end";
}

export function DropdownMenu({
  trigger,
  items,
  align = "end",
}: DropdownMenuProps) {
  const [isOpen, setIsOpen] = useState(false);
  const menuRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        setIsOpen(false);
      }
    };

    if (isOpen) {
      document.addEventListener("mousedown", handleClickOutside);
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [isOpen]);

  const alignClass = align === "start" ? "left-0" : "right-0";

  return (
    <div className="relative inline-block" ref={menuRef}>
      <div onClick={() => setIsOpen(!isOpen)}>{trigger}</div>

      {isOpen && (
        <div
          className={cn(
            "absolute z-50 mt-2 min-w-[180px] overflow-hidden rounded-lg border border-border bg-surface-2 py-1 shadow-lg",
            alignClass
          )}
        >
          {items.map((item, index) =>
            item === "separator" ? (
              <div key={index} className="my-1 h-px bg-border" />
            ) : (
              <button
                key={index}
                onClick={() => {
                  item.onClick?.();
                  setIsOpen(false);
                }}
                disabled={item.disabled}
                className={cn(
                  "flex w-full items-center gap-2 px-3 py-2 text-left text-sm transition-colors disabled:opacity-50",
                  item.danger
                    ? "text-error hover:bg-error/10"
                    : "text-text hover:bg-surface-3"
                )}
              >
                {item.icon && (
                  <span className="h-4 w-4 text-text-3">{item.icon}</span>
                )}
                {item.label}
              </button>
            )
          )}
        </div>
      )}
    </div>
  );
}
