"use client";

import { useState } from "react";
import { Button } from "./Button";
import { Avatar } from "./Avatar";
import { Select } from "./Select";
import { Input } from "./Input";
import { IconButton } from "./IconButton";
import { Badge } from "./Badge";
import { useToast } from "./Toast";
import { cn } from "./lib/utils";

// ===========================
// Roset Share Dialog
// ===========================
// Control plane-style sharing UI showcasing:
// - ACL permissions (owner/editor/viewer)
// - Signed URLs with expiration
// - Instant revoke capability
// - Team/group sharing
// - Audit trail awareness

interface User {
  id: string;
  name: string;
  email: string;
  role: "viewer" | "editor" | "owner";
  avatar?: string;
  addedAt?: string;
}

interface ShareLink {
  id: string;
  type: "public" | "restricted";
  permission: "view" | "download";
  expiresIn: string;
  accessCount?: number;
}

interface ShareDialogProps {
  fileName: string;
  filePath?: string;
  initialUsers?: User[];
  onShare?: (email: string, role: User["role"]) => void;
}

export function ShareDialog({ 
  fileName, 
  filePath = "/assets",
  initialUsers = [] 
}: ShareDialogProps) {
  const [users, setUsers] = useState<User[]>(initialUsers);
  const [emailStr, setEmailStr] = useState("");
  const [selectedRole, setSelectedRole] = useState<"viewer" | "editor">("viewer");
  const [shareLink, setShareLink] = useState<ShareLink | null>({
    id: "link-1",
    type: "public",
    permission: "view",
    expiresIn: "1 hour",
    accessCount: 0,
  });
  const [linkExpiry, setLinkExpiry] = useState<"1h" | "24h" | "7d" | "30d">("1h");
  const { addToast } = useToast();

  const handleInvite = () => {
    if (!emailStr || !emailStr.includes("@")) {
      addToast({ title: "Invalid email", description: "Please enter a valid email address", variant: "error" });
      return;
    }
    
    // Check if already invited
    if (users.some(u => u.email === emailStr)) {
      addToast({ title: "Already invited", description: "This user already has access", variant: "warning" });
      return;
    }

    setUsers([
      ...users,
      {
        id: Math.random().toString(36).slice(2),
        name: emailStr.split("@")[0],
        email: emailStr,
        role: selectedRole,
        addedAt: "just now",
      },
    ]);
    setEmailStr("");
    addToast({ 
      title: "Invite sent", 
      description: `${emailStr} can now ${selectedRole === "editor" ? "edit" : "view"} this file`,
      variant: "success" 
    });
  };

  const removeUser = (user: User) => {
    setUsers(users.filter((u) => u.id !== user.id));
    addToast({ 
      title: "Access revoked instantly", 
      description: `${user.name} can no longer access this file`,
      variant: "info" 
    });
  };

  const copyLink = () => {
    const mockUrl = `https://cdn.roset.io/s/${Math.random().toString(36).slice(2, 10)}`;
    navigator.clipboard.writeText(mockUrl);
    addToast({ 
      title: "Signed URL copied", 
      description: `Expires in ${shareLink?.expiresIn || "1 hour"}`,
      variant: "success" 
    });
  };

  const revokeLink = () => {
    setShareLink(null);
    addToast({ 
      title: "Link revoked", 
      description: "All existing links are now invalid",
      variant: "info" 
    });
  };

  const createLink = () => {
    const expiryMap = { "1h": "1 hour", "24h": "24 hours", "7d": "7 days", "30d": "30 days" };
    setShareLink({
      id: Math.random().toString(36).slice(2),
      type: "public",
      permission: "view",
      expiresIn: expiryMap[linkExpiry],
      accessCount: 0,
    });
    addToast({ 
      title: "Share link created", 
      description: `Signed URL generated • Expires in ${expiryMap[linkExpiry]}`,
      variant: "success" 
    });
  };

  const expiryOptions = [
    { label: "1 hour", value: "1h" },
    { label: "24 hours", value: "24h" },
    { label: "7 days", value: "7d" },
    { label: "30 days", value: "30d" },
  ];

  return (
    <div className="w-full max-w-md rounded-xl border border-border bg-surface-1 overflow-hidden">
      {/* Header */}
      <div className="border-b border-border bg-surface-2/50 px-4 py-3">
        <div className="flex items-center gap-3">
          <div className="flex h-9 w-9 items-center justify-center rounded-lg bg-accent/10 text-accent">
            <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6.001l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.368 2.684 3 3 0 00-5.368-2.684z" />
            </svg>
          </div>
          <div className="flex-1 min-w-0">
            <h3 className="text-sm font-semibold text-text truncate">Share {fileName}</h3>
            <p className="text-[11px] text-text-3 font-mono truncate">{filePath}/{fileName}</p>
          </div>
        </div>
      </div>

      {/* Invite Section */}
      <div className="p-4 border-b border-border">
        <label className="block text-[11px] font-medium uppercase tracking-wider text-text-3 mb-2">
          Invite people
        </label>
        <div className="flex gap-2">
          <div className="flex-1">
            <Input
              placeholder="colleague@company.com"
              value={emailStr}
              onChange={(e) => setEmailStr(e.target.value)}
              onKeyDown={(e) => e.key === "Enter" && handleInvite()}
              className="h-9 text-sm"
            />
          </div>
          <div className="w-24">
            <Select
              options={[
                { label: "Viewer", value: "viewer" },
                { label: "Editor", value: "editor" },
              ]}
              value={selectedRole}
              onChange={(e) => setSelectedRole(e.target.value as "viewer" | "editor")}
              className="h-9 text-xs"
            />
          </div>
          <Button onClick={handleInvite} size="sm" className="h-9 px-4">
            Invite
          </Button>
        </div>
      </div>

      {/* Users with Access */}
      <div className="p-4 border-b border-border">
        <div className="flex items-center justify-between mb-3">
          <label className="text-[11px] font-medium uppercase tracking-wider text-text-3">
            People with access
          </label>
          <Badge variant="outline" size="sm">{users.length} users</Badge>
        </div>
        
        <div className="space-y-1 max-h-[180px] overflow-y-auto">
          {users.length === 0 ? (
            <div className="text-center py-6 text-text-3 text-xs">
              No users have access yet
            </div>
          ) : (
            users.map((user) => (
              <div 
                key={user.id} 
                className="group flex items-center justify-between p-2 -mx-2 rounded-lg hover:bg-surface-2 transition-colors"
              >
                <div className="flex items-center gap-3 min-w-0">
                  <Avatar 
                    src={user.avatar} 
                    fallback={user.name[0]?.toUpperCase()} 
                    size="sm"
                  />
                  <div className="min-w-0">
                    <div className="flex items-center gap-2">
                      <span className="text-sm font-medium text-text truncate">
                        {user.name}
                      </span>
                      {user.id === "me" && (
                        <Badge variant="default" size="sm">You</Badge>
                      )}
                    </div>
                    <div className="text-[11px] text-text-3 truncate">{user.email}</div>
                  </div>
                </div>
                
                <div className="flex items-center gap-2">
                  {user.role === "owner" ? (
                    <Badge variant="success" size="sm">Owner</Badge>
                  ) : (
                    <>
                      <div className="w-20">
                        <Select
                          options={[
                            { label: "Viewer", value: "viewer" },
                            { label: "Editor", value: "editor" },
                          ]}
                          value={user.role}
                          onChange={(e) => {
                            const newRole = e.target.value as User["role"];
                            setUsers(users.map(u => u.id === user.id ? {...u, role: newRole} : u));
                            addToast({ 
                              title: "Permission updated", 
                              description: `${user.name} is now ${newRole === "editor" ? "an editor" : "a viewer"}`,
                              variant: "info" 
                            });
                          }}
                          className="h-7 text-[11px]"
                        />
                      </div>
                      <IconButton 
                        variant="ghost"
                        size="sm"
                        onClick={() => removeUser(user)}
                        aria-label="Revoke access"
                        className="opacity-0 group-hover:opacity-100 transition-opacity text-text-3 hover:text-error hover:bg-error/10"
                      >
                        <svg className="h-3.5 w-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                        </svg>
                      </IconButton>
                    </>
                  )}
                </div>
              </div>
            ))
          )}
        </div>
      </div>

      {/* Share Link Section */}
      <div className="p-4 bg-surface-2/30">
        <div className="flex items-center justify-between mb-3">
          <label className="text-[11px] font-medium uppercase tracking-wider text-text-3">
            Share link
          </label>
          {shareLink && (
            <span className="text-[10px] text-text-3 font-mono">
              {shareLink.accessCount || 0} views
            </span>
          )}
        </div>

        {shareLink ? (
          <div className="rounded-lg border border-border bg-surface-1 p-3">
            <div className="flex items-center gap-3">
              {/* Link icon */}
              <div className="flex h-8 w-8 items-center justify-center rounded-md bg-accent/10 text-accent flex-shrink-0">
                <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13.828 10.172a4 4 0 00-5.656 0l-4 4a4 4 0 105.656 5.656l1.102-1.101m-.758-4.899a4 4 0 005.656 0l4-4a4 4 0 00-5.656-5.656l-1.1 1.1" />
                </svg>
              </div>
              
              {/* Link info */}
              <div className="flex-1 min-w-0">
                <div className="text-sm font-medium text-text">Anyone with the link</div>
                <div className="flex items-center gap-2 mt-0.5">
                  <Badge variant="default" size="sm">
                    Can {shareLink.permission}
                  </Badge>
                  <span className="text-[10px] text-text-3">•</span>
                  <span className="text-[10px] text-warning font-medium">
                    Expires in {shareLink.expiresIn}
                  </span>
                </div>
              </div>
            </div>

            {/* Actions */}
            <div className="flex items-center gap-2 mt-3 pt-3 border-t border-border">
              <Button 
                variant="secondary" 
                size="sm" 
                onClick={copyLink}
                className="flex-1"
              >
                <svg className="h-3.5 w-3.5 mr-1.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                </svg>
                Copy Link
              </Button>
              <Button 
                variant="ghost" 
                size="sm" 
                onClick={revokeLink}
                className="text-error hover:bg-error/10"
              >
                Revoke
              </Button>
            </div>
          </div>
        ) : (
          /* Create new link */
          <div className="rounded-lg border border-dashed border-border bg-surface-1/50 p-4">
            <div className="flex items-center gap-3 mb-3">
              <div className="flex h-8 w-8 items-center justify-center rounded-md bg-surface-3 text-text-3 flex-shrink-0">
                <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13.828 10.172a4 4 0 00-5.656 0l-4 4a4 4 0 105.656 5.656l1.102-1.101m-.758-4.899a4 4 0 005.656 0l4-4a4 4 0 00-5.656-5.656l-1.1 1.1" />
                </svg>
              </div>
              <div className="flex-1">
                <div className="text-sm text-text-2">Create a shareable link</div>
                <div className="text-[11px] text-text-3">Generate a signed URL with expiration</div>
              </div>
            </div>
            <div className="flex items-center gap-2">
              <Select
                options={expiryOptions}
                value={linkExpiry}
                onChange={(e) => setLinkExpiry(e.target.value as typeof linkExpiry)}
                className="flex-1 h-8 text-xs"
              />
              <Button onClick={createLink} size="sm" className="h-8">
                Create Link
              </Button>
            </div>
          </div>
        )}
      </div>

      {/* Footer */}
      <div className="border-t border-border bg-surface-2/50 px-4 py-2">
        <p className="text-[10px] text-text-3 text-center">
          All access changes are logged in the audit trail
        </p>
      </div>
    </div>
  );
}
