"use client";

import { useState, useRef } from "react";
import { cn } from "./lib/utils";

interface CodeBlockProps {
  code?: string;
  children?: React.ReactNode;
  language?: string;
  filename?: string;
}

export function CodeBlock({
  code,
  children,
  language = "text",
  filename,
}: CodeBlockProps) {
  const contentRef = useRef<HTMLDivElement>(null);
  const [copied, setCopied] = useState(false);

  // Don't display "text" as a language - it's just a fallback
  const displayLanguage = language && language !== "text" ? language : null;

  const copyToClipboard = async () => {
    const textToCopy = code || contentRef.current?.textContent || "";
    
    if (textToCopy) {
      await navigator.clipboard.writeText(textToCopy);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    }
  };

  return (
    <div className="overflow-hidden rounded-xl border border-border shadow-sm my-4">
      {/* Header - darker background for contrast */}
      <div className="flex items-center justify-between bg-surface-3 px-4 py-2.5">
        <div className="flex items-center gap-3">
          {/* Window dots */}
          <div className="flex gap-1.5">
            <div className="h-3 w-3 rounded-full bg-[#ff5f57]" />
            <div className="h-3 w-3 rounded-full bg-[#febc2e]" />
            <div className="h-3 w-3 rounded-full bg-[#28c840]" />
          </div>
          {filename && (
            <span className="font-mono text-xs text-text-2">{filename}</span>
          )}
          {!filename && displayLanguage && (
            <span className="font-mono text-xs text-text-2">{displayLanguage}</span>
          )}
        </div>
        <button
          onClick={copyToClipboard}
          className="flex items-center gap-1.5 rounded-md px-2.5 py-1 text-xs font-medium text-text-2 transition-colors hover:bg-surface-2 hover:text-text"
          aria-label="Copy code"
        >
          {copied ? (
            <>
              <svg
                className="h-4 w-4 text-success"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M5 13l4 4L19 7"
                />
              </svg>
              Copied!
            </>
          ) : (
            <>
              <svg
                className="h-4 w-4"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z"
                />
              </svg>
              Copy
            </>
          )}
        </button>
      </div>
      {/* Code area */}
      <pre className="overflow-x-auto p-4 bg-surface-1">
        <div 
          ref={contentRef}
          className="font-mono text-sm leading-relaxed text-text"
        >
          {children || code}
        </div>
      </pre>
    </div>
  );
}
