import React from 'react'

interface StepsProps {
  children: React.ReactNode
}

export function Steps({ children }: StepsProps) {
  return (
    <div className="steps-container my-6">
      <ol className="list-none pl-0 space-y-6 counter-reset-step">
        {React.Children.map(children, (child, index) => (
          <li key={index} className="relative pl-10">
            <span className="absolute left-0 top-0 flex items-center justify-center w-7 h-7 rounded-full bg-accent text-white text-sm font-semibold">
              {index + 1}
            </span>
            <div>{child}</div>
          </li>
        ))}
      </ol>
    </div>
  )
}

interface StepProps {
  title: string
  children: React.ReactNode
}

export function Step({ title, children }: StepProps) {
  return (
    <div>
      <h4 className="font-semibold text-lg mb-2 text-text">{title}</h4>
      <div className="text-text-2">{children}</div>
    </div>
  )
}
