import { cn } from "./lib/utils";

interface Activity {
  id: string;
  actor: string;
  action: "move" | "delete" | "create" | "upload" | "authorize" | "restore" | "share" | "rename";
  target: string;
  timestamp: string;
}

interface ActivityLogProps {
  activities: Activity[];
  className?: string;
}

export function ActivityLog({ activities, className }: ActivityLogProps) {
  return (
    <div className={cn("rounded-xl border border-border bg-surface-1 p-4", className)}>
      <h3 className="mb-4 text-xs font-semibold uppercase tracking-wider text-text-3">
        Live Operation Log
      </h3>
      <div className="space-y-4">
        {activities.map((activity, index) => (
          <div key={activity.id} className="relative flex gap-3">
             {/* Timeline line */}
             {index !== activities.length - 1 && (
                 <div className="absolute left-[11px] top-6 bottom-[-20px] w-px bg-border"></div>
             )}
             
            {/* Status dot */}
            <div className="relative z-10 flex h-6 w-6 shrink-0 items-center justify-center rounded-full border border-border bg-surface-2">
                <div className={cn("h-2 w-2 rounded-full", getActionColor(activity.action))}></div>
            </div>

            <div className="flex-1 pt-0.5">
              <div className="flex items-center justify-between">
                <span className="text-sm font-medium text-text">
                  {activity.action === "authorize" ? "Authz Check" : capitalize(activity.action)}
                </span>
                <span className="font-mono text-xs text-text-3">{activity.timestamp}</span>
              </div>
              <p className="mt-0.5 text-xs text-text-3">
                <span className="text-text-2">{activity.actor}</span> {activity.action}d{" "}
                <span className="font-mono text-accent">{activity.target}</span>
              </p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

function getActionColor(action: Activity["action"]) {
  switch (action) {
    case "delete":
      return "bg-error";
    case "authorize":
      return "bg-warning";
    case "move":
    case "rename":
      return "bg-info";
    case "restore":
      return "bg-success";
    case "share":
      return "bg-accent";
    default:
      return "bg-accent";
  }
}

function capitalize(s: string) {
  return s.charAt(0).toUpperCase() + s.slice(1);
}
