import { ReactNode } from "react";
import { cn } from "./lib/utils";

interface StatCardProps {
  label: string;
  value: string | number;
  change?: {
    value: string | number;
    type: "increase" | "decrease" | "neutral";
  };
  icon?: ReactNode;
  className?: string;
}

export function StatCard({
  label,
  value,
  change,
  icon,
  className,
}: StatCardProps) {
  const changeColors = {
    increase: "text-success",
    decrease: "text-error",
    neutral: "text-text-3",
  };

  const changeIcons = {
    increase: (
      <svg className="h-3 w-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 10l7-7m0 0l7 7m-7-7v18" />
      </svg>
    ),
    decrease: (
      <svg className="h-3 w-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 14l-7 7m0 0l-7-7m7 7V3" />
      </svg>
    ),
    neutral: null,
  };

  return (
    <div
      className={cn("rounded-2xl border border-border bg-surface-2 p-6", className)}
    >
      <div className="flex items-start justify-between">
        <div>
          <p className="text-sm text-text-3">{label}</p>
          <p className="mt-2 text-3xl font-bold text-text">{value}</p>
          {change && (
            <div
              className={cn(
                "mt-2 flex items-center gap-1 text-xs font-medium",
                changeColors[change.type]
              )}
            >
              {changeIcons[change.type]}
              <span>{change.value}</span>
            </div>
          )}
        </div>
        {icon && (
          <div className="flex h-10 w-10 items-center justify-center rounded-lg bg-surface-3 text-text-2">
            {icon}
          </div>
        )}
      </div>
    </div>
  );
}
