'use client'

import React from 'react'

// ============================================================================
// Method Badge
// ============================================================================

const methodStyles: Record<string, React.CSSProperties> = {
  GET: { background: 'rgba(16, 185, 129, 0.1)', color: '#34d399', borderColor: 'rgba(16, 185, 129, 0.2)' },
  POST: { background: 'rgba(59, 130, 246, 0.1)', color: '#60a5fa', borderColor: 'rgba(59, 130, 246, 0.2)' },
  PUT: { background: 'rgba(245, 158, 11, 0.1)', color: '#fbbf24', borderColor: 'rgba(245, 158, 11, 0.2)' },
  PATCH: { background: 'rgba(249, 115, 22, 0.1)', color: '#fb923c', borderColor: 'rgba(249, 115, 22, 0.2)' },
  DELETE: { background: 'rgba(239, 68, 68, 0.1)', color: '#f87171', borderColor: 'rgba(239, 68, 68, 0.2)' },
}

interface MethodBadgeProps {
  method: 'GET' | 'POST' | 'PUT' | 'PATCH' | 'DELETE'
}

function MethodBadge({ method }: MethodBadgeProps) {
  return (
    <span
      style={{
        display: 'inline-flex',
        alignItems: 'center',
        borderRadius: '6px',
        border: '1px solid',
        padding: '4px 8px',
        fontFamily: 'ui-monospace, monospace',
        fontSize: '12px',
        fontWeight: 600,
        ...methodStyles[method],
      }}
    >
      {method}
    </span>
  )
}

// ============================================================================
// API Endpoint
// ============================================================================

interface ApiEndpointProps {
  method: 'GET' | 'POST' | 'PUT' | 'PATCH' | 'DELETE'
  path: string
  description?: string
  auth?: 'required' | 'optional' | 'none'
  children?: React.ReactNode
}

export function ApiEndpoint({ method, path, description, auth = 'required', children }: ApiEndpointProps) {
  return (
    <div
      style={{
        margin: '24px 0',
        overflow: 'hidden',
        borderRadius: '12px',
        border: '1px solid var(--border)',
        background: 'var(--surface-1)',
      }}
    >
      {/* Header */}
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          gap: '12px',
          borderBottom: '1px solid var(--border)',
          background: 'var(--surface-2)',
          padding: '12px 16px',
        }}
      >
        <MethodBadge method={method} />
        <code style={{ flex: 1, fontFamily: 'ui-monospace, monospace', fontSize: '14px', color: 'var(--text)' }}>
          {path}
        </code>
        {auth === 'required' && (
          <span style={{ display: 'flex', alignItems: 'center', gap: '4px', fontSize: '12px', color: 'var(--text-3)' }}>
            <svg style={{ width: '12px', height: '12px' }} fill="currentColor" viewBox="0 0 20 20">
              <path fillRule="evenodd" d="M5 9V7a5 5 0 0110 0v2a2 2 0 012 2v5a2 2 0 01-2 2H5a2 2 0 01-2-2v-5a2 2 0 012-2zm8-2v2H7V7a3 3 0 016 0z" clipRule="evenodd" />
            </svg>
            Auth required
          </span>
        )}
      </div>

      {/* Description */}
      {description && (
        <div style={{ borderBottom: '1px solid var(--border)', padding: '12px 16px' }}>
          <p style={{ fontSize: '14px', color: 'var(--text-2)', margin: 0 }}>{description}</p>
        </div>
      )}

      {/* Content (params, body, response) */}
      {children}
    </div>
  )
}

// ============================================================================
// Parameter Table
// ============================================================================

interface Parameter {
  name: string
  type: string
  required?: boolean
  description: string
  default?: string
}

interface ParameterTableProps {
  title?: string
  type?: 'path' | 'query' | 'body' | 'header'
  params: Parameter[]
}

export function ParameterTable({ title, type, params }: ParameterTableProps) {
  const typeLabels: Record<string, string> = {
    path: 'Path Parameters',
    query: 'Query Parameters',
    body: 'Request Body',
    header: 'Headers',
  }

  // Different colors for different parameter types
  const typeColors: Record<string, { bg: string; color: string }> = {
    path: { bg: 'rgba(168, 85, 247, 0.08)', color: '#a78bfa' },     // purple
    query: { bg: 'rgba(245, 158, 11, 0.08)', color: '#fbbf24' },    // amber
    body: { bg: 'rgba(59, 130, 246, 0.08)', color: '#60a5fa' },     // blue
    header: { bg: 'rgba(99, 102, 241, 0.08)', color: '#818cf8' },   // indigo
  }

  const colors = type ? typeColors[type] : { bg: 'rgba(107, 114, 128, 0.08)', color: '#9ca3af' }
  const displayTitle = title || (type ? typeLabels[type] : 'Parameters')

  return (
    <div style={{ borderBottom: '1px solid var(--border)' }}>
      {/* Header */}
      <div style={{ 
        padding: '12px 16px', 
        background: colors.bg, 
        borderBottom: '1px solid var(--border)' 
      }}>
        <h4 style={{ margin: 0, fontSize: '11px', fontWeight: 600, textTransform: 'uppercase', letterSpacing: '0.05em', color: colors.color }}>
          {displayTitle}
        </h4>
      </div>
      
      {/* Table */}
      <div style={{ padding: '16px', overflowX: 'auto' }}>
        <table style={{ width: '100%', fontSize: '14px', borderCollapse: 'collapse' }}>
          <thead>
            <tr style={{ borderBottom: '1px solid var(--border)', textAlign: 'left' }}>
              <th style={{ paddingBottom: '8px', paddingRight: '16px', fontWeight: 500, color: 'var(--text-2)' }}>Name</th>
              <th style={{ paddingBottom: '8px', paddingRight: '16px', fontWeight: 500, color: 'var(--text-2)' }}>Type</th>
              <th style={{ paddingBottom: '8px', paddingRight: '16px', fontWeight: 500, color: 'var(--text-2)' }}>Required</th>
              <th style={{ paddingBottom: '8px', fontWeight: 500, color: 'var(--text-2)' }}>Description</th>
            </tr>
          </thead>
          <tbody>
            {params.map((param, i) => (
              <tr key={param.name} style={{ borderBottom: i < params.length - 1 ? '1px solid rgba(39, 39, 42, 0.5)' : 'none' }}>
                <td style={{ padding: '10px 16px 10px 0' }}>
                  <code style={{ background: 'var(--surface-2)', padding: '3px 8px', borderRadius: '4px', fontFamily: 'ui-monospace, monospace', fontSize: '12px', color: 'var(--accent)' }}>
                    {param.name}
                  </code>
                </td>
                <td style={{ padding: '10px 16px 10px 0' }}>
                  <span style={{ fontFamily: 'ui-monospace, monospace', fontSize: '12px', color: 'var(--text-3)' }}>{param.type}</span>
                </td>
                <td style={{ padding: '10px 16px 10px 0' }}>
                  {param.required ? (
                    <span style={{ fontSize: '12px', fontWeight: 500, color: 'var(--warning)' }}>Required</span>
                  ) : (
                    <span style={{ fontSize: '12px', color: 'var(--text-3)' }}>Optional</span>
                  )}
                </td>
                <td style={{ padding: '10px 0', color: 'var(--text-2)' }}>
                  {param.description}
                  {param.default && (
                    <span style={{ marginLeft: '6px', color: 'var(--text-3)' }}>
                      (default: <code style={{ fontSize: '12px', background: 'var(--surface-2)', padding: '1px 4px', borderRadius: '3px' }}>{param.default}</code>)
                    </span>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  )
}

// ============================================================================
// Response Example
// ============================================================================

interface ResponseExampleProps {
  status: number
  description?: string
  json: object
}

export function ResponseExample({ status, description, json }: ResponseExampleProps) {
  const [copied, setCopied] = React.useState(false)
  const jsonString = JSON.stringify(json, null, 2)

  const statusColors: Record<number, string> = {
    200: '#34d399',
    201: '#34d399',
    204: '#34d399',
    400: '#fbbf24',
    401: '#f87171',
    403: '#f87171',
    404: '#fbbf24',
    409: '#fbbf24',
    500: '#f87171',
  }

  const handleCopy = async () => {
    await navigator.clipboard.writeText(jsonString)
    setCopied(true)
    setTimeout(() => setCopied(false), 2000)
  }

  return (
    <div style={{ borderBottom: '1px solid var(--border)' }}>
      <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', padding: '12px 16px', background: 'rgba(34, 197, 94, 0.08)', borderBottom: '1px solid var(--border)' }}>
        <h4 style={{ margin: 0, fontSize: '11px', fontWeight: 600, textTransform: 'uppercase', letterSpacing: '0.05em', color: '#34d399' }}>
          Response
          <span style={{ marginLeft: '8px', fontFamily: 'ui-monospace, monospace', color: statusColors[status] || 'var(--text-2)' }}>
            {status}
          </span>
          {description && <span style={{ marginLeft: '8px', fontWeight: 400, textTransform: 'none', color: 'var(--text-3)' }}>— {description}</span>}
        </h4>
        <button
          onClick={handleCopy}
          style={{
            display: 'flex',
            alignItems: 'center',
            gap: '4px',
            background: 'transparent',
            border: 'none',
            borderRadius: '4px',
            padding: '4px 8px',
            fontSize: '12px',
            color: 'var(--text-3)',
            cursor: 'pointer',
          }}
        >
          {copied ? (
            <>
              <svg style={{ width: '14px', height: '14px', color: 'var(--success)' }} fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
              </svg>
              Copied
            </>
          ) : (
            <>
              <svg style={{ width: '14px', height: '14px' }} fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
              </svg>
              Copy
            </>
          )}
        </button>
      </div>
      <pre style={{ overflow: 'auto', borderRadius: '8px', background: 'var(--surface-2)', padding: '16px', fontFamily: 'ui-monospace, monospace', fontSize: '12px', lineHeight: 1.6, color: 'var(--text)', margin: 0 }}>
        <code>{jsonString}</code>
      </pre>
    </div>
  )
}

