'use client'

import { useEffect, useState, useCallback, useRef } from 'react'
import { cn } from './lib/utils'

interface SearchResult {
  url: string
  title: string
  excerpt: string
}

interface SearchTriggerProps {
  onClick?: () => void
  className?: string
}

export function SearchTrigger({ onClick, className }: SearchTriggerProps) {
  return (
    <button
      onClick={onClick}
      className={cn(
        "group flex items-center gap-2 rounded-lg border border-border bg-surface-1 px-2.5 py-1.5 transition-all hover:border-accent/50 hover:bg-surface-2",
        className
      )}
      aria-label="Search documentation"
    >
      <svg 
        className="h-3.5 w-3.5 text-text-3 group-hover:text-text-2" 
        fill="none" 
        stroke="currentColor" 
        viewBox="0 0 24 24"
      >
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
      </svg>
      <span className="text-xs text-text-3 group-hover:text-text-2">Search</span>
      <kbd className="hidden items-center gap-0.5 rounded border border-border bg-surface-2 px-1.5 py-0.5 font-mono text-[10px] text-text-3 sm:flex">
        <span>⌘</span>
        <span>K</span>
      </kbd>
    </button>
  )
}

export function Search() {
  const [isOpen, setIsOpen] = useState(false)
  const [query, setQuery] = useState('')
  const [results, setResults] = useState<SearchResult[]>([])
  const [selectedIndex, setSelectedIndex] = useState(0)
  const [pagefind, setPagefind] = useState<any>(null)
  const inputRef = useRef<HTMLInputElement>(null)
  const listRef = useRef<HTMLUListElement>(null)

  // Load Pagefind
  useEffect(() => {
    async function loadPagefind() {
      try {
        // @ts-ignore
        const pf = await import(/* webpackIgnore: true */ '/pagefind/pagefind.js')
        await pf.init()
        setPagefind(pf)
      } catch (e) {
        console.warn('Pagefind not available (run build first)')
      }
    }
    loadPagefind()
  }, [])

  // ⌘K shortcut
  useEffect(() => {
    function handleKeyDown(e: KeyboardEvent) {
      if ((e.metaKey || e.ctrlKey) && e.key === 'k') {
        e.preventDefault()
        setIsOpen(true)
      }
      if (e.key === 'Escape') {
        setIsOpen(false)
        setQuery('')
      }
      if (!isOpen) return

      // Navigate with arrows
      if (e.key === 'ArrowDown') {
        e.preventDefault()
        setSelectedIndex((prev) => (prev < results.length - 1 ? prev + 1 : 0))
      }
      if (e.key === 'ArrowUp') {
        e.preventDefault()
        setSelectedIndex((prev) => (prev > 0 ? prev - 1 : results.length - 1))
      }
      // Select with Enter
      if (e.key === 'Enter' && results[selectedIndex]) {
        window.location.href = results[selectedIndex].url
        setIsOpen(false)
      }
    }
    document.addEventListener('keydown', handleKeyDown)
    return () => document.removeEventListener('keydown', handleKeyDown)
  }, [isOpen, results, selectedIndex])

  // Focus input when opened
  useEffect(() => {
    if (isOpen) {
      requestAnimationFrame(() => {
        inputRef.current?.focus()
        setSelectedIndex(0)
      })
    }
  }, [isOpen])

  // Scroll selected into view
  useEffect(() => {
    if (listRef.current && results.length > 0) {
      const selectedEl = listRef.current.children[selectedIndex] as HTMLElement
      if (selectedEl) {
        selectedEl.scrollIntoView({ block: 'nearest' })
      }
    }
  }, [selectedIndex, results.length])

  // Search
  const search = useCallback(async (q: string) => {
    setSelectedIndex(0)
    if (!pagefind || !q) {
      setResults([])
      return
    }
    const searchResults = await pagefind.search(q)
    const data = await Promise.all(
      searchResults.results.slice(0, 8).map((r: any) => r.data())
    )
    setResults(data.map((d: any) => {
      // Pagefind returns URLs like /server/pages/guides/leases.html
      // We need to extract just /guides/leases
      let url = d.url
      // Remove /server/pages prefix if present
      url = url.replace(/^\/server\/pages/, '')
      // Remove .html extension
      url = url.replace(/\.html$/, '')
      // Handle index pages
      url = url.replace(/\/index$/, '/')
      // Ensure leading slash
      if (!url.startsWith('/')) url = '/' + url
      
      return {
        url,
        title: d.meta?.title || url,
        excerpt: d.excerpt,
      }
    }))
  }, [pagefind])

  useEffect(() => {
    const timeout = setTimeout(() => search(query), 200)
    return () => clearTimeout(timeout)
  }, [query, search])

  // Trigger button
  if (!isOpen) {
    return <SearchTrigger onClick={() => setIsOpen(true)} />
  }

  return (
    <div className="fixed inset-0 z-50 flex items-start justify-center pt-[15vh]">
      {/* Backdrop */}
      <div 
        className="absolute inset-0 bg-black/70 backdrop-blur-sm" 
        onClick={() => { setIsOpen(false); setQuery(''); }} 
      />
      
      {/* Dialog */}
      <div className="relative w-full max-w-xl overflow-hidden rounded-xl border border-border bg-surface-1 shadow-2xl shadow-black/50">
        {/* Search input */}
        <div className="flex items-center gap-3 border-b border-border px-4">
          <svg className="w-5 h-5 flex-shrink-0 text-text-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
          </svg>
          <input
            ref={inputRef}
            type="text"
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            placeholder="Search documentation..."
            className="flex-1 border-none bg-transparent py-4 text-base text-text outline-none ring-0 placeholder:text-text-3"
            style={{ boxShadow: 'none' }}
          />
          <kbd className="rounded border border-border bg-surface-2 px-1 py-0.5 font-mono text-[9px] text-text-3">ESC</kbd>
        </div>

        {/* Results */}
        {results.length > 0 && (
          <ul ref={listRef} className="max-h-[60vh] overflow-y-auto p-2">
            {results.map((result, i) => {
              // Create breadcrumb from URL (e.g. /guides/sdk -> guides / sdk)
              const path = result.url.split('/').filter(p => p && !p.startsWith('#')).join(' / ') || 'Home'
              
              return (
                <li key={i}>
                  <a
                    href={result.url}
                    onClick={() => setIsOpen(false)}
                    onMouseEnter={() => setSelectedIndex(i)}
                    className={cn(
                      "group flex items-start gap-3 rounded-lg px-3 py-3 transition-all",
                      i === selectedIndex 
                        ? "bg-accent text-white" 
                        : "text-text hover:bg-surface-2"
                    )}
                  >
                    {/* File Icon */}
                    <span className={cn(
                      "mt-0.5 flex h-5 w-5 flex-shrink-0 items-center justify-center rounded",
                      i === selectedIndex ? "text-white/80" : "text-text-3"
                    )}>
                      <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                      </svg>
                    </span>

                    <div className="flex-1 min-w-0">
                      {/* Breadcrumb Path */}
                      <div className={cn(
                        "mb-0.5 text-[10px] font-medium uppercase tracking-wider",
                        i === selectedIndex ? "text-white/70" : "text-text-3"
                      )}>
                        {path}
                      </div>

                      {/* Title */}
                      <div className="font-medium text-sm truncate">
                        {result.title}
                      </div>

                      {/* Excerpt */}
                      <div 
                        className={cn(
                          "mt-1 text-xs line-clamp-1",
                          i === selectedIndex ? "text-white/70" : "text-text-3"
                        )}
                        dangerouslySetInnerHTML={{ __html: result.excerpt }}
                      />
                    </div>

                    {/* Enter Arrow (only visible on selection) */}
                    {i === selectedIndex && (
                      <svg className="h-4 w-4 text-white/70 flex-shrink-0 mt-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                      </svg>
                    )}
                  </a>
                </li>
              )
            })}
          </ul>
        )}

        {query && results.length === 0 && (
          <div className="flex flex-col items-center justify-center py-12 text-center">
            <svg className="mb-3 h-10 w-10 text-text-3/50" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M9.172 16.172a4 4 0 015.656 0M9 10h.01M15 10h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
            <p className="text-sm text-text-3">No results for "{query}"</p>
            <p className="mt-1 text-xs text-text-3/70">Try searching for something else</p>
          </div>
        )}

        {/* Footer */}
        <div className="flex items-center justify-between border-t border-border bg-surface-2/50 px-4 py-2 text-xs text-text-3">
          <div className="flex items-center gap-3">
            <span className="flex items-center gap-1">
              <kbd className="rounded border border-border bg-surface-2 px-1 py-0.5 font-mono text-[10px]">↑</kbd>
              <kbd className="rounded border border-border bg-surface-2 px-1 py-0.5 font-mono text-[10px]">↓</kbd>
              <span className="ml-1">to navigate</span>
            </span>
            <span className="flex items-center gap-1">
              <kbd className="rounded border border-border bg-surface-2 px-1 py-0.5 font-mono text-[10px]">↵</kbd>
              <span className="ml-1">to select</span>
            </span>
          </div>
          <span className="flex items-center gap-1">
            <kbd className="rounded border border-border bg-surface-2 px-1 py-0.5 font-mono text-[10px]">⌘K</kbd>
            <span className="ml-1">to toggle</span>
          </span>
        </div>
      </div>
    </div>
  )
}
