"use client";

import { useState, useEffect, useRef, ReactNode, useCallback, useMemo } from "react";
import { cn } from "./lib/utils";

interface Command {
  id: string;
  label: string;
  description?: string;
  icon?: ReactNode;
  action: () => void;
  keywords?: string[];
  shortcut?: string;
}

interface CommandPaletteProps {
  commands: Command[];
  placeholder?: string;
}

export function CommandPalette({
  commands,
  placeholder = "Type a command or search...",
}: CommandPaletteProps) {
  const [isOpen, setIsOpen] = useState(false);
  const [query, setQuery] = useState("");
  const [selectedIndex, setSelectedIndex] = useState(0);
  const inputRef = useRef<HTMLInputElement>(null);
  const listRef = useRef<HTMLDivElement>(null);

  const filteredCommands = useMemo(() => {
    const searchText = query.toLowerCase();
    return commands.filter((cmd) =>
      cmd.label.toLowerCase().includes(searchText) ||
      cmd.description?.toLowerCase().includes(searchText) ||
      cmd.keywords?.some((k) => k.toLowerCase().includes(searchText))
    );
  }, [commands, query]);

  const handleQueryChange = useCallback((newQuery: string) => {
    setQuery(newQuery);
    setSelectedIndex(0);
  }, []);

  const executeCommand = useCallback((cmd: Command) => {
    cmd.action();
    setIsOpen(false);
    setQuery("");
  }, []);

  const handleKeyDown = useCallback(
    (e: KeyboardEvent) => {
      // Open with Cmd+K or Ctrl+K
      if ((e.metaKey || e.ctrlKey) && e.key === "k") {
        e.preventDefault();
        setIsOpen(true);
      }

      if (!isOpen) return;

      // Close with Escape
      if (e.key === "Escape") {
        setIsOpen(false);
        setQuery("");
      }

      // Navigate with arrows
      if (e.key === "ArrowDown") {
        e.preventDefault();
        setSelectedIndex((prev) =>
          prev < filteredCommands.length - 1 ? prev + 1 : 0
        );
      }

      if (e.key === "ArrowUp") {
        e.preventDefault();
        setSelectedIndex((prev) =>
          prev > 0 ? prev - 1 : filteredCommands.length - 1
        );
      }

      // Execute with Enter
      if (e.key === "Enter" && filteredCommands[selectedIndex]) {
        executeCommand(filteredCommands[selectedIndex]);
      }
    },
    [isOpen, filteredCommands, selectedIndex, executeCommand]
  );

  useEffect(() => {
    document.addEventListener("keydown", handleKeyDown);
    return () => document.removeEventListener("keydown", handleKeyDown);
  }, [handleKeyDown]);

  useEffect(() => {
    if (isOpen) {
      requestAnimationFrame(() => {
        inputRef.current?.focus();
        setSelectedIndex(0);
      });
    }
  }, [isOpen]);

  // Scroll selected item into view
  useEffect(() => {
    if (listRef.current && filteredCommands.length > 0) {
      const selectedEl = listRef.current.children[selectedIndex] as HTMLElement;
      if (selectedEl) {
        selectedEl.scrollIntoView({ block: "nearest" });
      }
    }
  }, [selectedIndex, filteredCommands.length]);

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-start justify-center pt-[15vh]">
      {/* Backdrop */}
      <div
        className="absolute inset-0 bg-black/70 backdrop-blur-sm"
        onClick={() => {
          setIsOpen(false);
          setQuery("");
        }}
      />

      {/* Dialog */}
      <div className="relative w-full max-w-xl overflow-hidden rounded-xl border border-border bg-surface-1 shadow-2xl shadow-black/50">
        {/* Search input */}
        <div className="flex items-center gap-3 border-b border-border px-4">
          <svg
            className="h-5 w-5 flex-shrink-0 text-text-3"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
            />
          </svg>
          <input
            ref={inputRef}
            type="text"
            value={query}
            onChange={(e) => handleQueryChange(e.target.value)}
            placeholder={placeholder}
            className="flex-1 border-none bg-transparent py-4 text-base text-text outline-none ring-0 placeholder:text-text-3 focus:border-none focus:outline-none focus:ring-0 focus:shadow-none focus-visible:ring-0 focus-visible:shadow-none"
            style={{ boxShadow: "none", outline: "none" }}
          />
          <div className="flex items-center gap-1.5">
            <kbd className="rounded border border-border bg-surface-2 px-1.5 py-0.5 font-mono text-[10px] text-text-3">
              ESC
            </kbd>
          </div>
        </div>

        {/* Results */}
        <div ref={listRef} className="max-h-80 overflow-y-auto p-2">
          {filteredCommands.length === 0 ? (
            <div className="flex flex-col items-center justify-center py-12 text-center">
              <svg
                className="mb-3 h-10 w-10 text-text-3/50"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={1.5}
                  d="M9.172 16.172a4 4 0 015.656 0M9 10h.01M15 10h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
                />
              </svg>
              <p className="text-sm text-text-3">No results found</p>
              <p className="mt-1 text-xs text-text-3/70">
                Try searching for something else
              </p>
            </div>
          ) : (
            filteredCommands.map((cmd, index) => (
              <button
                key={cmd.id}
                onClick={() => executeCommand(cmd)}
                onMouseEnter={() => setSelectedIndex(index)}
                className={cn(
                  "flex w-full items-center gap-3 rounded-lg px-3 py-2.5 text-left transition-colors",
                  index === selectedIndex
                    ? "bg-accent text-white"
                    : "text-text hover:bg-surface-2"
                )}
              >
                {/* Icon */}
                {cmd.icon ? (
                  <span
                    className={cn(
                      "flex h-8 w-8 flex-shrink-0 items-center justify-center rounded-lg",
                      index === selectedIndex
                        ? "bg-white/20 text-white"
                        : "bg-surface-2 text-text-2"
                    )}
                  >
                    {cmd.icon}
                  </span>
                ) : (
                  <span
                    className={cn(
                      "flex h-8 w-8 flex-shrink-0 items-center justify-center rounded-lg",
                      index === selectedIndex
                        ? "bg-white/20 text-white"
                        : "bg-surface-2 text-text-3"
                    )}
                  >
                    <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                    </svg>
                  </span>
                )}

                {/* Content */}
                <div className="flex-1 min-w-0">
                  <p className="truncate text-sm font-medium">{cmd.label}</p>
                  {cmd.description && (
                    <p
                      className={cn(
                        "truncate text-xs",
                        index === selectedIndex ? "text-white/70" : "text-text-3"
                      )}
                    >
                      {cmd.description}
                    </p>
                  )}
                </div>

                {/* Shortcut */}
                {cmd.shortcut && (
                  <kbd
                    className={cn(
                      "rounded border px-1.5 py-0.5 font-mono text-[10px]",
                      index === selectedIndex
                        ? "border-white/30 text-white/70"
                        : "border-border text-text-3"
                    )}
                  >
                    {cmd.shortcut}
                  </kbd>
                )}
              </button>
            ))
          )}
        </div>

        {/* Footer */}
        <div className="flex items-center justify-between border-t border-border bg-surface-2/50 px-4 py-2 text-xs text-text-3">
          <div className="flex items-center gap-3">
            <span className="flex items-center gap-1">
              <kbd className="rounded border border-border bg-surface-2 px-1 py-0.5 font-mono text-[10px]">↑</kbd>
              <kbd className="rounded border border-border bg-surface-2 px-1 py-0.5 font-mono text-[10px]">↓</kbd>
              <span className="ml-1">to navigate</span>
            </span>
            <span className="flex items-center gap-1">
              <kbd className="rounded border border-border bg-surface-2 px-1 py-0.5 font-mono text-[10px]">↵</kbd>
              <span className="ml-1">to select</span>
            </span>
          </div>
          <span className="flex items-center gap-1">
            <kbd className="rounded border border-border bg-surface-2 px-1 py-0.5 font-mono text-[10px]">⌘K</kbd>
            <span className="ml-1">to toggle</span>
          </span>
        </div>
      </div>
    </div>
  );
}

// Hook to trigger command palette
export function useCommandPalette() {
  const open = useCallback(() => {
    document.dispatchEvent(
      new KeyboardEvent("keydown", { key: "k", metaKey: true })
    );
  }, []);

  return { open };
}
