"use client";

import { createContext, useContext, useState, useCallback, ReactNode, useEffect } from "react";
import { cn } from "./lib/utils";

// ===========================
// Roset Toast System
// ===========================
// Control plane-style notifications
// - Minimal, purposeful motion (120-180ms)
// - Border + surface contrast (not heavy shadows)
// - Functional colors from design system
// - Developer-first copy

interface Toast {
  id: string;
  title: string;
  description?: string;
  variant: "success" | "error" | "warning" | "info";
  action?: {
    label: string;
    onClick: () => void;
  };
}

interface ToastContextType {
  toasts: Toast[];
  addToast: (props: Omit<Toast, "id">) => void;
  removeToast: (id: string) => void;
}

const ToastContext = createContext<ToastContextType | null>(null);

export function useToast() {
  const context = useContext(ToastContext);
  if (!context) {
    throw new Error("useToast must be used within a ToastProvider");
  }
  return context;
}

export function ToastProvider({ children }: { children: ReactNode }) {
  const [toasts, setToasts] = useState<Toast[]>([]);

  const addToast = useCallback(
    (props: Omit<Toast, "id">) => {
      const id = Math.random().toString(36).slice(2);
      const { title, variant = "info", description, action } = props;
      setToasts((prev) => [...prev, { id, title, description, variant, action }]);

      // Auto-remove after 4 seconds (faster for snappy UX)
      setTimeout(() => {
        setToasts((prev) => prev.filter((t) => t.id !== id));
      }, 4000);
    },
    []
  );

  const removeToast = useCallback((id: string) => {
    setToasts((prev) => prev.filter((t) => t.id !== id));
  }, []);

  return (
    <ToastContext.Provider value={{ toasts, addToast, removeToast }}>
      {children}
      <ToastContainer toasts={toasts} removeToast={removeToast} />
    </ToastContext.Provider>
  );
}

function ToastContainer({
  toasts,
  removeToast,
}: {
  toasts: Toast[];
  removeToast: (id: string) => void;
}) {
  return (
    <div className="fixed bottom-4 right-4 z-50 flex flex-col gap-2 pointer-events-none">
      {toasts.map((toast) => (
        <ToastItem key={toast.id} toast={toast} onClose={() => removeToast(toast.id)} />
      ))}
    </div>
  );
}

// Variant configurations following Roset design system
const variantConfig = {
  success: {
    borderColor: "border-success/30",
    bgColor: "bg-success/5",
    iconBg: "bg-success/10",
    iconColor: "text-success",
    accentBar: "bg-success",
  },
  error: {
    borderColor: "border-error/30",
    bgColor: "bg-error/5",
    iconBg: "bg-error/10",
    iconColor: "text-error",
    accentBar: "bg-error",
  },
  warning: {
    borderColor: "border-warning/30",
    bgColor: "bg-warning/5",
    iconBg: "bg-warning/10",
    iconColor: "text-warning",
    accentBar: "bg-warning",
  },
  info: {
    borderColor: "border-info/30",
    bgColor: "bg-info/5",
    iconBg: "bg-info/10",
    iconColor: "text-info",
    accentBar: "bg-info",
  },
};

// Clean, minimal icons
const icons = {
  success: (
    <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24" strokeWidth={2.5}>
      <path strokeLinecap="round" strokeLinejoin="round" d="M5 13l4 4L19 7" />
    </svg>
  ),
  error: (
    <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24" strokeWidth={2.5}>
      <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
    </svg>
  ),
  warning: (
    <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24" strokeWidth={2.5}>
      <path strokeLinecap="round" strokeLinejoin="round" d="M12 9v2m0 4h.01" />
    </svg>
  ),
  info: (
    <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24" strokeWidth={2.5}>
      <path strokeLinecap="round" strokeLinejoin="round" d="M13 16h-1v-4h-1m1-4h.01" />
    </svg>
  ),
};

function ToastItem({ toast, onClose }: { toast: Toast; onClose: () => void }) {
  const [isVisible, setIsVisible] = useState(false);
  const [progress, setProgress] = useState(100);
  const config = variantConfig[toast.variant];

  useEffect(() => {
    // Trigger animation in (120ms delay for smooth mount)
    const showTimer = setTimeout(() => setIsVisible(true), 10);
    
    // Progress bar animation
    const startTime = Date.now();
    const duration = 4000;
    const progressInterval = setInterval(() => {
      const elapsed = Date.now() - startTime;
      const remaining = Math.max(0, 100 - (elapsed / duration) * 100);
      setProgress(remaining);
      if (remaining === 0) clearInterval(progressInterval);
    }, 50);

    return () => {
      clearTimeout(showTimer);
      clearInterval(progressInterval);
    };
  }, []);

  const handleClose = () => {
    setIsVisible(false);
    // Wait for animation out (180ms)
    setTimeout(onClose, 180);
  };

  return (
    <div
      className={cn(
        "pointer-events-auto relative overflow-hidden flex items-start gap-3 min-w-[320px] max-w-[400px] rounded-lg border bg-surface-1 backdrop-blur-sm p-3 pr-10 shadow-sm transition-all duration-[180ms] ease-out",
        config.borderColor,
        config.bgColor,
        isVisible ? "translate-x-0 opacity-100" : "translate-x-4 opacity-0"
      )}
      role="alert"
    >
      {/* Left accent bar */}
      <div className={cn("absolute left-0 top-0 bottom-0 w-0.5", config.accentBar)} />

      {/* Icon */}
      <div className={cn("flex-shrink-0 flex h-6 w-6 items-center justify-center rounded-md", config.iconBg, config.iconColor)}>
        {icons[toast.variant]}
      </div>
      
      {/* Content */}
      <div className="flex-1 min-w-0 pt-0.5">
        <p className="text-sm font-medium text-text leading-tight">{toast.title}</p>
        {toast.description && (
          <p className="mt-0.5 text-xs text-text-3 leading-relaxed">{toast.description}</p>
        )}
        {toast.action && (
          <button
            onClick={() => {
              toast.action?.onClick();
              handleClose();
            }}
            className="mt-2 text-xs font-medium text-accent hover:text-accent-hover transition-colors"
          >
            {toast.action.label}
          </button>
        )}
      </div>

      {/* Close button */}
      <button
        onClick={handleClose}
        className="absolute top-2 right-2 rounded-md p-1.5 text-text-3 hover:text-text hover:bg-surface-3 transition-colors"
        aria-label="Dismiss"
      >
        <svg className="h-3.5 w-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
        </svg>
      </button>

      {/* Progress bar at bottom */}
      <div className="absolute bottom-0 left-0 right-0 h-0.5 bg-border/30">
        <div 
          className={cn("h-full transition-all duration-75 ease-linear", config.accentBar)}
          style={{ width: `${progress}%` }}
        />
      </div>
    </div>
  );
}
