import { cn } from "./lib/utils";

interface SkeletonProps {
  className?: string;
  variant?: "text" | "circular" | "rectangular";
  width?: string | number;
  height?: string | number;
}

export function Skeleton({
  className,
  variant = "text",
  width,
  height,
}: SkeletonProps) {
  const baseStyles = "animate-pulse bg-surface-3";

  const variants = {
    text: "h-4 w-full rounded",
    circular: "rounded-full",
    rectangular: "rounded-lg",
  };

  const style: React.CSSProperties = {};
  if (width) style.width = typeof width === "number" ? `${width}px` : width;
  if (height) style.height = typeof height === "number" ? `${height}px` : height;

  return (
    <div
      className={cn(baseStyles, variants[variant], className)}
      style={style}
    />
  );
}

// Pre-composed skeleton patterns
export function SkeletonCard() {
  return (
    <div className="rounded-2xl border border-border bg-surface-2 p-6 space-y-4">
      <Skeleton variant="rectangular" height={20} width="60%" />
      <Skeleton variant="text" />
      <Skeleton variant="text" width="80%" />
    </div>
  );
}

export function SkeletonAvatar({ size = "md" }: { size?: "sm" | "md" | "lg" }) {
  const sizes = { sm: 32, md: 40, lg: 48 };
  return <Skeleton variant="circular" width={sizes[size]} height={sizes[size]} />;
}

export function SkeletonButton() {
  return <Skeleton variant="rectangular" height={40} width={100} />;
}
