"use client";

import { useState } from "react";
import { Button } from "./Button";
import { Badge } from "./Badge";
import { useToast } from "./Toast";
import { cn } from "./lib/utils";

interface ApiKey {
  id: string;
  name: string;
  prefix: string;
  created: string;
  lastUsed: string;
  scopes: string[];
}

export function ApiKeyManager() {
  const [keys, setKeys] = useState<ApiKey[]>([
    { id: "1", name: "Production Server", prefix: "roset_live_8x...", created: "Oct 24", lastUsed: "Just now", scopes: ["read", "write"] },
    { id: "2", name: "Dev Laptop", prefix: "roset_test_9a...", created: "Nov 12", lastUsed: "2d ago", scopes: ["read"] },
  ]);
  const { addToast } = useToast();

  const handleRevoke = (id: string) => {
    setKeys(keys.filter((k) => k.id !== id));
    addToast({ title: "Key revoked", variant: "error" });
  };

  const handleCreate = () => {
    const newKey: ApiKey = {
      id: Math.random().toString(),
      name: "New Integration",
      prefix: "roset_live_nw...",
      created: "Just now",
      lastUsed: "Never",
      scopes: ["read"],
    };
    setKeys([newKey, ...keys]);
    addToast({ title: "API Key Created", description: "Make sure to copy it now.", variant: "success" });
  };

  return (
    <div className="w-full rounded-xl border border-border bg-surface-1">
      <div className="flex items-center justify-between border-b border-border p-4">
        <div>
           <h3 className="text-sm font-semibold text-text">API Keys</h3>
           <p className="text-xs text-text-3">Manage access tokens for your project.</p>
        </div>
        <Button size="sm" onClick={handleCreate}>+ Create New Key</Button>
      </div>

      <div className="divide-y divide-border">
        {keys.map((key) => (
          <div key={key.id} className="flex items-center justify-between p-4">
            <div className="space-y-1">
              <div className="flex items-center gap-2">
                <span className="font-medium text-text text-sm">{key.name}</span>
                {key.scopes.map(scope => (
                  <Badge key={scope} variant={scope === "write" ? "warning" : "default"} size="sm">
                    {scope}
                  </Badge>
                ))}
              </div>
              <div className="flex items-center gap-3 text-xs text-text-3 font-mono">
                <span>{key.prefix}</span>
                <span className="text-text-3/50">•</span>
                <span>Last used {key.lastUsed}</span>
              </div>
            </div>
            
            <div className="flex items-center gap-2">
               <Button variant="ghost" size="sm" onClick={() => addToast({ title: "Copied to clipboard", variant: "info"})}>
                  Copy
               </Button>
               <Button 
                  variant="ghost"
                  size="sm"
                  onClick={() => handleRevoke(key.id)}
                  className="text-error hover:text-error hover:bg-error/10"
               >
                  Revoke
               </Button>
            </div>
          </div>
        ))}
      </div>
      {keys.length === 0 && (
          <div className="p-8 text-center text-sm text-text-3">No active API keys.</div>
      )}
    </div>
  );
}
