import { cn } from "./lib/utils";

interface AvatarProps {
  src?: string;
  alt?: string;
  fallback?: string;
  size?: "sm" | "md" | "lg";
  status?: "online" | "away" | "busy" | "offline";
  className?: string;
}

export function Avatar({
  src,
  alt = "Avatar",
  fallback,
  size = "md",
  status,
  className,
}: AvatarProps) {
  const sizes = {
    sm: "h-8 w-8 text-xs",
    md: "h-10 w-10 text-sm",
    lg: "h-12 w-12 text-base",
  };

  const statusColors = {
    online: "bg-success",
    away: "bg-warning",
    busy: "bg-error",
    offline: "bg-text-3",
  };

  const statusSizes = {
    sm: "h-2 w-2",
    md: "h-2.5 w-2.5",
    lg: "h-3 w-3",
  };

  const initials = fallback
    ? fallback
        .split(" ")
        .map((n) => n[0])
        .join("")
        .toUpperCase()
        .slice(0, 2)
    : "?";

  return (
    <div className={cn("relative inline-flex", className)}>
      {src ? (
        <img
          src={src}
          alt={alt}
          className={cn(sizes[size], "rounded-full object-cover ring-2 ring-border")}
        />
      ) : (
        <div
          className={cn(
            sizes[size],
            "flex items-center justify-center rounded-full bg-surface-3 font-medium text-text ring-2 ring-border"
          )}
        >
          {initials}
        </div>
      )}
      {status && (
        <span
          className={cn(
            "absolute bottom-0 right-0 rounded-full ring-2 ring-background",
            statusSizes[size],
            statusColors[status]
          )}
        />
      )}
    </div>
  );
}
