'use client'

import React, { useState } from 'react'

interface CodeBlockCopyProps {
  code: string
  language?: string
}

export function CodeBlockCopy({ code, language = 'bash' }: CodeBlockCopyProps) {
  const [copied, setCopied] = useState(false)

  const handleCopy = async () => {
    await navigator.clipboard.writeText(code)
    setCopied(true)
    setTimeout(() => setCopied(false), 2000)
  }

  return (
    <div className="relative group">
      <pre className={`language-${language} rounded-lg overflow-x-auto`}>
        <code>{code}</code>
      </pre>
      <button
        onClick={handleCopy}
        className="absolute top-2 right-2 px-2 py-1 text-xs bg-surface-3 hover:bg-surface-2 text-text rounded opacity-0 group-hover:opacity-100 transition-opacity"
      >
        {copied ? '✓ Copied' : 'Copy'}
      </button>
    </div>
  )
}
